import React from "react"
import {useState, useEffect} from "react";
// import * as $ from "jquery";
import { authEndpoint, clientId, redirectUri, scopes } from "./config";
import hash from "./hash";
// import Player from "./Player";
// import Visualizer from "./Visualizer";
// import logo from "./logo.svg";
// import recordplayer from "./FullVinyl6.png"
import axios from 'axios'
import AlbumImage from "./AlbumImage";
import WoodenShelf from "./shelf2.png"
import "./App.css";
import Visualizer from "./Visualizer";

const AppClone = () => {
  //all the states
  const [token, setToken] = useState(null)
  const [albums, setAlbums] = useState([])
  const [me, setMe] = useState(null)
  const [is_playing, setIs_playing] = useState("false")
  const [progress_ms, setProgress_ms] = useState(0)
  const [noData, setNoData] = useState(true)
  const [item, setItem] = useState(
    {
      album: {
        images: [{ url: "" }]
      },
      name: "",
      artists: [{ name: "" }],
      duration_ms: 0
    }
  )
  const [colors, setColors] = useState(null)
  const [newsong, setNewSong] = useState(null)

  //useEffect calls
  //useEffect to get token
  useEffect(() => {
    let _token = hash.access_token
    if(_token) {
      setToken(_token)
      console.log("token", token)
    }
  })

  //useEffect to get top tracks
  useEffect(() => 
  {
    // console.log("inEffect")
    // console.log("token in access", token)
    if(token)
    {
      // console.log("in-branch")
      axios
      .get("https://api.spotify.com/v1/me/top/tracks", {
        params: {
          limit: 9,
          time_range: "medium_term"
        },
        headers: {
          "Authorization": `Bearer ${token}`
        }
      })
      .then(response => {
        // console.log("response happened", response.data)
        setAlbums(response.data.items)
        // console.log(albums)
      })
      .catch(error => {
        // console.log("getting player", error)
      })
    }
  }, [token])
  
  //useEffect to get name of user
  useEffect(() => 
  {
    if(token)
    {
      // console.log("in-branch")
      axios
      .get("https://api.spotify.com/v1/me", {
        headers: {
          "Authorization": `Bearer ${token}`
        }
      })
      .then(response => {
        // console.log("response happened", response.data)
        setMe(response.data)
        // console.log(me)
      })
      .catch(error => {
        // console.log("getting player", error)
      })
    }
  }, [token])

  //useEffect to get currently playing track
  const getCurrentlyPlaying = async() => {
    if(token)
    {
      console.log("in-player-branch")
      axios.get("https://api.spotify.com/v1/me/player", {
        headers: {
          "Authorization": `Bearer ${token}`
        }
      })
      .then(response => {
        console.log("response happened for player", response)
        if(response.data)
        {
          setItem(response.data.item)
          setIs_playing(response.data.is_playing)
          setProgress_ms(response.data.progress_ms)
          setNoData(false)
          console.log("newsong",response.data.item.name)
          if(response.data.item.name !== newsong)
          {
            setNewSong(response.data.item.name)
          }
        }
      }
      )
    }
  }

  useEffect(() => {
    getCurrentlyPlaying()
    const interval = setInterval(getCurrentlyPlaying, 5000)
    return() => clearInterval(interval)
  }, [token])

  //how to get color
  useEffect(() => {
    axios.get('https://random-palette-generator.p.rapidapi.com/palette/1/3', {
      headers: {
        'X-RapidAPI-Key': "837ae2e27dmsh5ccbd62313b76a0p16e94bjsndc7dbc227cc9",
        'X-RapidAPI-Host': 'random-palette-generator.p.rapidapi.com'
      }
    }
    )
    .then(response => {
      console.log('colordata',response)
      setColors(response.data.data)
    })
  }, [newsong])

  //useEffect to get colors

  return (
    <>
    <div className="App">
      <header className="App-header">
        {!token && (
          <a
            className="btn btn--loginApp-link"
            href={`${authEndpoint}?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scopes.join(
              "%20"
            )}&response_type=token&show_dialog=true`}
          >
            Login to Spotify
          </a>
        )}
        {token && albums && me && !noData && colors &&(
          <>
          {/* <h1>Top Played Songs in the Last 4 Weeks</h1> */}
          {/* <div className = "Shelf-Container">
            <img className = "woodenShelfone" src = {WoodenShelf} alt = "shelf"/>
            {/* <img className = "woodenShelftwo" src = {WoodenShelf} alt = "shelf"/>
            <img className = "woodenShelfthree" src = {WoodenShelf} alt = "shelf"/>}
          </div> */}
          {/* <img className = "woodenShelfone" src = {WoodenShelf} alt = "shelf"/> */}
          {/* <h1>Songs Stuck in {me.display_name}'s Ear</h1>
          <div className = "Album-Grid">
            {albums.map(album => 
              {
                return (
                  <AlbumImage album = {album}/>
                )
              }
            )
            }
          </div> */}
          <Visualizer item = {item} playing = {is_playing} color = {colors}/>
          </>
        )}
        {noData && token &&(
          <p>
            You need to be playing a song on Spotify, for something to appear here.
          </p>
        )}
      </header>
    </div>
    <p className = "Main-Subcaption">Developed by <a href = "https://masatonandate.github.io/masato_website/index.html" target = "_blank" rel = "noopener noreferrer">Masato Nandate </a></p>
  </>
  )
}

export default AppClone;